* {
  box-sizing: border-box;
  color: #333;
  transition: background 0.3s ease, color 0.5s ease;
}

body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  min-width: 100vw;
  transition: 0.5s;
  z-index: 100;
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  overflow-x: hidden;
}
body #toggleTheme {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 100;
  border-radius: 50%;
  padding: 5px;
  background: transparent;
  border: 0px;
  cursor: pointer;
  transition: 0.2s;
}
body #toggleTheme.hover-moon:hover {
  transform: scale(1.2);
  box-shadow: 0 0 2px #000, 0 0 3px #000, 0 0 4px #000, 0 0 5px #000, 0 0 10px #000, 0 0 20px #000;
}
body #toggleTheme.hover-sun:hover {
  transform: scale(1.2);
  box-shadow: 0 0 2px #fff, 0 0 3px #fff, 0 0 4px #fff, 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #fff;
}
body #toggleTheme .fa-moon {
  color: black;
  font-size: 30px;
}
body #toggleTheme .fa-sun {
  color: white;
  font-size: 30px;
}

#root {
  min-height: 100vh;
  min-width: 100vw;
}
.dark #root {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  background-size: 100% 100%;
  background-position: top left;
  background-repeat: no-repeat;
}
.light #root {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  background-size: 0% 0%;
  background-position: top left;
  background-repeat: no-repeat;
}

header {
  display: flex;
  align-content: center;
  justify-content: center;
  width: 1100px;
  margin: 0 auto;
  padding: 20px 0px 0px 0px;
}
header a {
  color: #333;
  font-size: 3px;
  text-decoration: none;
  font-weight: bold;
  display: block;
  padding-bottom: 10px;
  display: inline-block;
  background-repeat: no-repeat;
  filter: brightness(150%);
  background-position: 45% 45%;
  width: 500px;
  height: 135px;
  overflow: hidden;
  text-indent: -9999px;
  margin: 20px 0;
}
.dark header a {
  background-image: url(./Images/logo24.png);
}
.light header a {
  background-image: url(./Images/logo24.png);
}

.order-container {
  display: flex;
  width: 1200px;
  margin: 0 auto;
  flex-direction: row;
  position: relative;
}
.order-container form {
  border-radius: 6px;
  background: #faeff0;
  box-shadow: 0px 0px 12px #aaa, 0px 0px 12px #fff;
  width: 360px;
  margin: 0px 25px 0px 0px;
  padding: 35px 15px 15px 15px;
  display: flex;
  flex-direction: column;
  height: 350px;
}
.dark .order-container form {
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  box-shadow: 0px 0px 12px #777, 0px 0px 12px #000;
}

.light .order-container form {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  box-shadow: 0px 0px 12px #aaa, 0px 0px 12px #fff;
}

.order-container form #submit {
  width: 30%;
  font-size: 20px;
  margin: 0 35%;
}
.order-container form label {
  margin: 0 0 20px 0;
  display: inline-block;
  font-size: 20px;
}
.light .order-container form label {
  color: white;
}
.order-container form label > * {
  display: block;
  width: 100%;
  font-size: 20px;
  margin: 10px 0px;
}

.food {
  border-radius: 6px;
  width: 715px;
  padding: 15px;
  margin-bottom: 25px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: background 0.5s ease, transform 0.5 ease;
}
.dark .food {
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  box-shadow: 0px 0px 12px #777, 0px 0px 12px #000;
}
.light .food {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  box-shadow: 0px 0px 12px #aaa, 0px 0px 12px #fff;
}
.food.active {
  transform: scale(1.03);
  box-shadow: 0px 0px 20px 5px #33ee33 !important;
}
.food-image {
  background-color: transparent;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.dark .food h1,
.dark .food h2 {
  color: black;
}
.light .food h1,
.light .food h2 {
  color: white;
}
.food div {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.food div img {
  width: 100%;
  height: 100%;
  transform: scale(1.1);
}

.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
.modal-content {
  position: relative;
  padding: 20px;
  width: 80%; /* Could be more or less, depending on screen size */
  width: 900px;
  margin: 0 auto;
  padding: 35px;
  display: flex;
  flex-direction: column;
  height: 600px;
  margin-top: 70px;
  -webkit-animation-name: animatetop;
          animation-name: animatetop;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  background-blend-mode: screen;
}
.light .modal-content {
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
}
.dark .modal-content {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
}
.modal-header {
  padding: 2px 16px;
  text-align: center;
}
.modal-header h2 {
  font-size: 40px;
  text-transform: uppercase;
}
.dark .modal-header h2 {
  color: white;
}
.light .modal-header h2 {
  color: black;
}
.modal-header h2 .logo {
  background-image: url(./Images/logo3.jpeg.png);
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(150%);
  background-position: 45% 45%;
  display: inline-block;
  width: 50px;
  height: 50px;
  position: relative;
  top: 10px;
}
.modal-body {
  min-height: 250px;
  margin: 10px 0;
  display: flex !important;
  flex-direction: column !important;
}
.modal-body label {
  margin: 10px 0;
  font-size: 20px;
  color: white;
}
.dark .modal-body label {
  color: white;
}
.light .modal-body label {
  color: black;
}
.modal-body label input,
.modal-body label select {
  margin: 5px 0px;
  font-size: 20px;
  width: 100%;
}
.modal .add-Image {
  min-height: 80px;
}
.modal .add-Image .image-btns {
  padding: 2px;
  display: inline-block;
  background-color: white;
  cursor: pointer;
}
.dark .modal .add-Image .image-btns {
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  color: black;
}
.light .modal .add-Image .image-btns {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  color: white;
}
.modal .add-Image .image-btns.active {
  background: transparent;
  transform: scale(1.1);
}
.dark .modal .add-Image .image-btns.active {
  color: white;
  border: 1px solid white;
}
.light .modal .add-Image .image-btns.active {
  color: black;
  border: 1px solid black;
}
.modal .add-Image .img-Input {
  display: none;
  height: 30px;
}
.modal .add-Image .img-Input.active {
  display: block;
}
.modal .add-Image .img-Input#image {
  cursor: pointer;
  margin-top: 10px;
}
.modal-footer {
  padding: 2px 16px;
}
.modal-footer button {
  cursor: pointer;
  transition: background-color 0.2s ease;
  width: 30%;
  font-size: 20px;
  margin: 0 35%;
  padding: 5px 10px;
  border-radius: 4px;
}
.dark .modal-footer button {
  border: 1px solid white;
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  color: black;
}
.light .modal-footer button {
  border: 1px solid black;
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  color: white;
}
.modal-footer button:hover {
  background: transparent;
}
.dark .modal-footer button:hover {
  color: white;
  border: 1px solid white;
}
.light .modal-footer button:hover {
  color: black;
  border: 1px solid black;
}
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}
.close:hover, .close:focus {
  text-decoration: none;
  cursor: pointer;
}
.dark .close:hover, .dark .close:focus {
  color: white;
}
.light .close:hover, .light .close:focus {
  color: black;
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
.Login-Page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Login-Page p {
  font-size: 50px;
  color: white;
}
.light .Login-Page p {
  font-size: 50px;
  color: black;
}

.login-btns button {
  font-size: 20px;
  padding: 8px 20px;
  margin: 0 10px;
  cursor: pointer;
  border-radius: 2px;
  transition: transform 0.2s ease, background 0.2s ease;
}
.light .login-btns button {
  border: 1px solid black;
  color: white;
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
}
.light .login-btns button:hover {
  color: black;
}
.dark .login-btns button {
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  border: 1px solid white;
  color: black;
}
.dark .login-btns button:hover {
  color: white;
}
.login-btns button:hover {
  transform: scale(1.1);
  font-size: 20px;
  background: transparent;
}

#staffBtnsParent {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#staffBtnsParent .staffBtn {
  font-size: 40px;
  margin: 0 30px;
  padding: 20px 30px;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.5s ease, color 0.5s ease;
}
.dark #staffBtnsParent .staffBtn {
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
  border: 2px solid white;
  color: black;
}
.dark #staffBtnsParent .staffBtn:hover {
  color: white;
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
}
.light #staffBtnsParent .staffBtn {
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgb(35, 35, 35) 80%);
  border: 2px solid black;
  color: white;
}
.light #staffBtnsParent .staffBtn:hover {
  color: black;
  background: linear-gradient(90deg, rgb(230, 230, 241) 37%, rgb(225, 231, 232) 80%);
}
#staffBtnsParent .staffBtn:hover {
  background-color: transparent;
}/*# sourceMappingURL=styleFood.css.map */