$-bg-dark: linear-gradient(
  90deg,
  rgba(2, 0, 36, 1) 0%,
  rgba(35, 35, 35, 1) 80%
);
$-border-dark: 1px solid black;
$-font-dark: black;
$-font-dark-hover: black;
$-box-shadow-dark: 0px 0px 12px #aaa, -0px -0px 12px #fff;

$-bg-light: linear-gradient(
  90deg,
  rgba(230, 230, 241, 1) 37%,
  rgba(225, 231, 232, 1) 80%
);
$-border-light: 1px solid white;
$-font-light: white;
$-font-light-hover: white;
$-box-shadow-light: 0px 0px 12px #777, -0px -0px 12px #000;

* {
  box-sizing: border-box;
  color: #333;
  transition: background 0.3s ease, color 0.5s ease;
}

body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  min-width: 100vw;
  transition: 0.5s;
  z-index: 100;
  background: $-bg-light;
  overflow-x: hidden;

  #toggleTheme {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 100;
    border-radius: 50%;
    padding: 5px;
    background: transparent;
    border: 0px;
    cursor: pointer;
    transition: 0.2s;

    &.hover-moon {
      &:hover {
        transform: scale(1.2);
        box-shadow: 0 0 2px #000, 0 0 3px #000, 0 0 4px #000, 0 0 5px #000,
          0 0 10px #000, 0 0 20px #000;
      }
    }

    &.hover-sun {
      &:hover {
        transform: scale(1.2);
        box-shadow: 0 0 2px #fff, 0 0 3px #fff, 0 0 4px #fff, 0 0 5px #fff,
          0 0 10px #fff, 0 0 20px #fff;
      }
    }

    .fa-moon {
      color: black;
      font-size: 30px;
    }

    .fa-sun {
      color: white;
      font-size: 30px;
    }
  }
}

#root {
  min-height: 100vh;
  min-width: 100vw;

  .dark & {
    background: $-bg-dark;
    background-size: 100% 100%;
    background-position: top left;
    background-repeat: no-repeat;
  }

  .light & {
    background: $-bg-dark;
    background-size: 0% 0%;
    background-position: top left;
    background-repeat: no-repeat;
  }
}

header {
  display: flex;
  align-content: center;
  justify-content: center;
  width: 1100px;
  margin: 0 auto;
  padding: 20px 0px 0px 0px;

  a {
    color: #333;
    font-size: 3px;
    text-decoration: none;
    font-weight: bold;
    display: block;
    padding-bottom: 10px;
    display: inline-block;

    background-repeat: no-repeat;
    filter: brightness(150%);
    background-position: 45% 45%;
    width: 500px;
    height: 135px;
    overflow: hidden;
    text-indent: -9999px;
    margin: 20px 0;

    .dark & {
      background-image: url(./Images/logo24.png);
    }

    .light & {
      background-image: url(./Images/logo24.png);
    }
  }
}

.order-container {
  display: flex;
  width: 1200px;
  margin: 0 auto;
  flex-direction: row;
  position: relative;

  form {
    border-radius: 6px;
    background: #faeff0;
    box-shadow: 0px 0px 12px #aaa, -0px -0px 12px #fff;
    width: 360px;
    margin: 0px 25px 0px 0px;
    padding: 35px 15px 15px 15px;
    display: flex;
    flex-direction: column;
    height: 350px;

    @at-root .dark & {
      background: $-bg-light;
      box-shadow: $-box-shadow-light;
    }
    @at-root .light & {
      background: $-bg-dark;
      box-shadow: $-box-shadow-dark;
    }

    #submit {
      width: 30%;
      font-size: 20px;
      margin: 0 calc(50% - 15%);
    }

    label {
      margin: 0 0 20px 0;
      display: inline-block;
      font-size: 20px;

      .light & {
        color: white;
      }

      > * {
        display: block;
        width: 100%;
        font-size: 20px;
        margin: 10px 0px;
      }
    }
  }
}

.food {
  border-radius: 6px;
  width: 715px;
  padding: 15px;
  margin-bottom: 25px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: background 0.5s ease, transform 0.5 ease;

  .dark & {
    background: $-bg-light;
    box-shadow: $-box-shadow-light;
  }

  .light & {
    background: $-bg-dark;
    box-shadow: $-box-shadow-dark;
  }

  &.active {
    transform: scale(1.03);
    box-shadow: 0px 0px 20px 5px #33ee33 !important;
  }

  &-image {
    background-color: transparent;
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  h1,
  h2 {
    .dark & {
      color: $-font-dark;
    }

    .light & {
      color: $-font-light;
    }
  }

  div {
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 100%;
      height: 100%;
      transform: scale(1.1);
    }
  }
}

.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */

  &-content {
    position: relative;
    padding: 20px;
    width: 80%; /* Could be more or less, depending on screen size */
    width: 900px;
    margin: 0 auto;
    padding: 35px;
    display: flex;
    flex-direction: column;
    height: 600px;
    margin-top: 70px;
    animation-name: animatetop;
    animation-duration: 0.3s;
    background-blend-mode: screen;

    .light & {
      background: $-bg-light;
    }

    .dark & {
      background: $-bg-dark;
    }
  }

  &-header {
    padding: 2px 16px;
    text-align: center;

    h2 {
      font-size: 40px;
      text-transform: uppercase;

      .dark & {
        color: $-font-light;
      }
      .light & {
        color: $-font-dark;
      }

      .logo {
        background-image: url(./Images/logo3.jpeg.png);
        background-repeat: no-repeat;
        background-size: cover;
        filter: brightness(150%);
        background-position: 45% 45%;
        display: inline-block;
        width: 50px;
        height: 50px;
        position: relative;
        top: 10px;
      }
    }
  }

  &-body {
    min-height: 250px;
    margin: 10px 0;
    display: flex !important;
    flex-direction: column !important;

    label {
      margin: 10px 0;
      font-size: 20px;
      color: white;

      .dark & {
        color: $-font-light;
      }

      .light & {
        color: $-font-dark;
      }

      input,
      select {
        margin: 5px 0px;
        font-size: 20px;
        width: 100%;
      }
    }
  }

  .add-Image {
    min-height: 80px;

    .image-btns {
      padding: 2px;
      display: inline-block;
      background-color: white;
      cursor: pointer;

      .dark & {
        background: $-bg-light;
        color: $-font-dark;
      }

      .light & {
        background: $-bg-dark;
        color: $-font-light;
      }

      &.active {
        background: transparent;
        transform: scale(1.1);

        .dark & {
          color: $-font-light;
          border: $-border-light;
        }

        .light & {
          color: $-font-dark;
          border: $-border-dark;
        }
      }
    }

    .img-Input {
      display: none;
      height: 30px;

      &.active {
        display: block;
      }

      &#image {
        cursor: pointer;
        margin-top: 10px;
      }
    }
  }

  &-footer {
    padding: 2px 16px;

    button {
      cursor: pointer;
      transition: background-color 0.2s ease;
      width: 30%;
      font-size: 20px;
      margin: 0 calc(50% - 15%);
      padding: 5px 10px;
      border-radius: 4px;

      .dark & {
        border: $-border-light;
        background: $-bg-light;
        color: $-font-dark;
      }

      .light & {
        border: $-border-dark;
        background: $-bg-dark;
        color: $-font-light;
      }

      &:hover {
        background: transparent;

        .dark & {
          color: $-font-light;
          border: $-border-light;
        }

        .light & {
          color: $-font-dark;
          border: $-border-dark;
        }
      }
    }
  }

  @at-root .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;

    &:hover,
    &:focus {
      text-decoration: none;
      cursor: pointer;

      .dark & {
        color: $-font-light;
      }
      .light & {
        color: $-font-dark;
      }
    }
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

.Login-Page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    font-size: 50px;
    color: $-font-light;

    .light & {
      font-size: 50px;
      color: $-font-dark;
    }
  }
}

.login-btns {
  button {
    font-size: 20px;
    padding: 8px 20px;
    margin: 0 10px;
    cursor: pointer;
    border-radius: 2px;
    transition: transform 0.2s ease, background 0.2s ease;

    .light & {
      border: $-border-dark;
      color: $-font-light;
      background: $-bg-dark;

      &:hover {
        color: $-font-dark;
      }
    }

    .dark & {
      background: $-bg-light;
      border: $-border-light;
      color: $-font-dark;

      &:hover {
        color: $-font-light;
      }
    }

    &:hover {
      transform: scale(1.1);
      font-size: 20px;
      background: transparent;
    }
  }
}

#staffBtnsParent {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;

  .staffBtn {
    font-size: 40px;
    margin: 0 30px;
    padding: 20px 30px;
    cursor: pointer;
    border-radius: 8px;
    transition: background-color 0.5s ease, color 0.5s ease;

    .dark & {
      background: $-bg-light;
      border: 2px solid white;
      color: $-font-dark;

      &:hover {
        color: $-font-light;
        background: $-bg-dark;
      }
    }

    .light & {
      background: $-bg-dark;
      border: 2px solid black;
      color: $-font-light;

      &:hover {
        color: $-font-dark;
        background: $-bg-light;
      }
    }

    &:hover {
      background-color: transparent;
    }
  }
}
